import React from 'react'
import axios from 'axios';
import './reset.scss'
import './app.scss'
import Button from './Components/Button/button'

const sendData = () => {
  alert('Сделан запрос')
}

const getTemperature = () => {
  alert('Температура: 25 градусов')
}

const getParams = async () => {
  try {
    const response = await axios.get('/params');
    alert(response.data);
    console.log(response.status);
    console.log(response.statusText);
  } catch (error) {
    console.error(error);
  }
}

function App() {
  return (
    <div className="app"> 
      <div className="container">
        <h1 className="app-title">Control panel</h1>
        <ul className="btn-list">
          <li className="btn-item"><Button text="Send data" onClick={sendData}/></li>
          <li className="btn-item"><Button text="Temperature" onClick={getTemperature}/></li>
          <li className="btn-item"><Button text="Get parameters" onClick={getParams}/></li>
        </ul>
      </div>
    </div>
  );
}

export default App